
.add {
  max-width: 960px;
  margin: 20px auto;
  background: #fff;
  height: 500px;
}
.add .title {
    text-align: left;
    height: 40px;
    background: #eee;
    padding-left: 20px;
    line-height: 40px;
}
.add-title, .add-content, .button {
  text-align: left;
  padding: 20px;
}
.add-title input {
  width: 500px;
  border: 1px solid #333;
  height: 35px;
}
.add-content textarea {
  width: 500px;
  border: 1px solid #333;
}
.button a {
  display: block;
  background: #8729E7;
  width: 300px;
  color: white;
  text-align: center;
  line-height: 40px;
  height: 40px;
}



