
.toast {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0;
  left: 0;
  z-index: 999;
}
.toastShoadow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
  z-index: 9999;
}
.toast-box {
  background: #fff;
  position: fixed;
  width: 400px;
  height: 240px;
  top: 50%;
  left: 50%;
  margin-left: -180px;
  margin-top: -150px;
  z-index: 99999;
  border-radius: 10px;
}
.toast-text {
  padding: 20px;
  text-align: center;
  font-size: 16px;
  height: 80px;
  line-height: 100px;
}
.toast-shure {
  padding: 20px;
  height: 100px;
}
.toast-shure a {
    float: left;
    padding: 0px 20px;
    margin: 0px 20px;
    width: 100px;
    height: 40px;
    line-height: 40px;
    color: white;
    font-size: 16px;
    text-decoration: none;
}
.toast-btn {
  background: #8729E7;
}
.toast-btn-cancel {
  background: #666;
}



