
.header {
  background: #8729E7;
  height: 50px;
  width: 100%;
}
.header-name {
  line-height: 2em;
  height: 50px;
  color: #fff;
  line-height: 50px;
  width: 960px;
  margin: 0 auto;
  text-align: left;
}
.header-name .l {
    float: left;
}
.header-name .r {
    float: right;
}
.header-name .r a {
      color: #fff;
      text-decoration: none;
}
.header-name .r li {
      float: left;
      padding: 0 10px;
}
.header-name .r img {
      width: 16px;
      height: 16px;
      border-radius: 2px;
      border: 2px solid #fff;
}
.header-name .clear {
    clear: both;
}



