
.comment {
  text-align: left;
  padding: 20px;
  border-top: 10px solid #eee;
}
.comment .comment-list {
    line-height: 2em;
    border-bottom: 1px solid #eee;
    margin-bottom: 20px;
    padding-bottom: 20px;
}
.comment-title img {
  width: 30px;
  height: 30px;
  border-radius: 30px;
  display: block;
  float: left;
}
.comment-user {
  color: #8729E7;
  text-decoration: none;
}
.topic {
  max-width: 960px;
  background: #fff;
  margin: 20px auto;
  width: 100%;
}
.index-top {
  height: 80px;
  background: #f6f6f6;
  line-height: 40px;
  text-align: left;
  padding-left: 20px;
}
.index-top .index-top-em {
    height: 15px;
    font-size: 11px;
}
.index-top .index-top-em ul {
      margin: 0;
      padding: 0;
      padding-left: 10px;
}
.index-top .index-top-em li {
      float: left;
      list-style: initial;
      padding-right: 20px;
}
.index-top .index-top-title {
    height: 25px;
    font-size: 20px;
    font-weight: bold;
    padding-top: 15px;
}
.index-list2 {
  text-align: left;
  padding: 20px;
  line-height: 25px;
}
.index-list2 h1, .index-list2 h2 {
    border-bottom: 1px solid #eee;
    padding-bottom: 20px;
}
.index-list2 ul {
    padding-left: 20px;
}
.index-list2 li {
    list-style: initial;
}
.index-list2 table tr td {
    padding: 2px 5px;
    border: 1px solid #eee;
}
.index-list2 th {
    padding: 2px 5px;
    border: 1px solid #eee;
}
.index-list2 blockquote {
    background: #eee;
    padding: 1px 10px;
}
.index-list2 a {
    color: #8729E7;
    text-decoration: none;
}
.index-list2 a:hover {
    text-decoration: underline;
}
.index-list2 img {
  display: block;
  max-width: 100%;
}



